.contact-main {
  width: 100%;
}

.basic-contact {
  margin-left: 5%;
  margin-right: 5%;
}

.contact-heading-div {
  display: flex;
}

.blog-heading-div {
  display: flex;
}

.address-heading-div {
  display: flex;
  margin-top: 80px;
}

.blog-heading-div > * {
  flex: 1;
}

.contact-heading-div > * {
  flex: 1;
}

.address-heading-div > * {
  flex: 1;
}

.contact-heading-img-div {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 4rem;
  margin-bottom: 10rem;
}

.blog-heading-img-div {
  align-items: center;
  justify-content: center;
}

.contact-heading-text-div {
  text-align: center;
  margin-top: 4rem;
  margin-bottom: 10rem;
}

.blog-heading-text-div {
  text-align: center;
}

.address-heading-text-div {
  text-align: center;
}

.contact-heading-img-div > img {
  max-width: 100%;
  height: auto;
  border-radius: 50%;
  border: 0.5rem solid #a1a1a1;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.3);
  transition: border-color 0.3s ease-in-out;
}

.contact-heading-img-div > img:hover {
  border-color: #007bff;
}

.blog-heading-img-div > * {
  max-width: 100%;
  height: auto;
}

.contact-heading-text {
  font-size: 56px;
  font-weight: 400;
  font-family: "Google Sans Medium";
  line-height: 1.1;
  text-align: center;
  margin-top: 60px;
}

.blog-heading-text {
  font-size: 56px;
  font-weight: 400;
  font-family: "Google Sans Medium";
  line-height: 1.1;
  text-align: center;
  margin-top: 40px;
}

.address-heading-text {
  font-size: 30px;
  font-family: "Google Sans Regular";
  text-align: center;
  margin-bottom: 10px;
}

.contact-header-detail-text {
  font-size: 20px;
  line-height: 30px;
  margin-top: 0;
  margin-left: 20px;
  margin-right: 20px;
  font-family: "Google Sans Medium";
}

.blog-header-detail-text {
  font-size: 20px;
  line-height: 30px;
  margin-top: 0;
  margin-left: 20px;
  margin-right: 20px;
  font-family: "Google Sans Medium";
}

.resume-btn-div {
  width: 100%;
  display: flex;
  justify-content: center;
}

.blogsite-btn-div {
  width: 100%;
  display: flex;
  justify-content: center;
}

.address-btn-div {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 0;
}

@media (max-width: 1380px) {
  .contact-heading-text {
    font-size: 50px;
    margin-top: 60px;
  }

  .blog-heading-text {
    font-size: 50px;
    margin-top: 40px;
  }
}

@media (max-width: 768px) {
  .contact-heading-text {
    font-size: 30px;
    margin-top: 0;
  }

  .blog-heading-text {
    font-size: 30px;
    margin-top: 0;
  }

  .address-heading-text {
    font-size: 20px;
  }

  .blog-heading-div {
    flex-direction: column-reverse;
    margin-top: 60px;
  }

  .address-heading-div {
    flex-direction: column;
    margin-top: 60px;
  }

  .blog-heading-text-div {
    margin-top: 40px;
  }

  .address-heading-text-div {
    margin-top: 40px;
  }

  .contact-heading-div {
    flex-direction: column;
  }

  .contact-header-detail-text {
    font-size: 16px;
    margin-left: 0;
    margin-right: 0;
    line-height: normal;
    text-align: center;
  }

  .blog-header-detail-text {
    font-size: 16px;
    margin-left: 0;
    margin-right: 0;
    line-height: normal;
    text-align: center;
  }
}

.contact-container {
  background-color: #1e2631;
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.profile-image-container {
  width: 500px;
  height: 500px;
  border-radius: 50%;
  overflow: hidden;
  border: 4px solid #0088ff;
  box-shadow: 0 0 20px rgba(0, 136, 255, 0.3);
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contact-title {
  color: #c9d1d9;
  font-size: 3rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.contact-subtitle {
  color: #8b949e;
  font-size: 1.2rem;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto 2rem;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.social-icon {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  transition: transform 0.3s ease;
  color: #fff;
  text-decoration: none;
}

.social-icon:hover {
  transform: translateY(-3px);
}

.github {
  background-color: #333;
}

.linkedin {
  background-color: #0077b5;
}

.google {
  background-color: #ea4335;
}

.instagram {
  background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
}

.resume-button {
  background-color: #30363d !important;
  color: #c9d1d9 !important;
  padding: 12px 30px !important;
  font-size: 1rem !important;
  border-radius: 8px !important;
  text-transform: none !important;
  transition: all 0.3s ease !important;
}

.resume-button:hover {
  background-color: #3c444d !important;
  transform: translateY(-2px);
}

@media (max-width: 960px) {
  .contact-container > div {
    flex-direction: column;
    text-align: center;
    gap: 3rem;
  }

  .profile-image-container {
    width: 300px;
    height: 300px;
    margin: 0 auto;
  }

  .contact-title {
    font-size: 2.5rem;
  }
}
